<template>
  <v-container fluid>
    <notifications
      class="mt-1"
      group="flush"
      position="top center"
    />

    <v-layout
      align-center
      fill-height
      justify-space-between
      row>
      <v-flex
        class="mr-3"
        sm1>
        <v-switch
          v-model="full_style"
          label="宽屏"
        />
      </v-flex>
      <v-flex>
        <v-text-field
          v-model="post.title"
          label="标题"
        />
      </v-flex>
    </v-layout>

    <v-layout
      align-center
      fill-height
      justify-space-between
      row>
      <v-flex
        v-show="!full_style"
        xs12>
        <v-text-field
          v-model="post.alias"
          label="地址栏别称"
        />
      </v-flex>
    </v-layout>

    <v-layout
      align-start
      fill-height
      justify-start
      row>

      <v-flex
        v-show="!full_style"
        align-start
        class="mr-3"
        column
        fill-height
        justify-start
        xs3>
        <v-select
          :items="type_items"
          v-model="post.type"
          item-text="k"
          item-value="v"
          label="类型"
        />

        <v-select
          :items="st_items"
          v-model="post.status"
          item-text="k"
          item-value="v"
          label="状态"
        />

        <v-select
          v-show="post.type==`Article`"
          :items="tag_items"
          v-model="post.tags"
          chips
          item-text="v"
          item-value="k"
          label="标签"
          multiple
        />

        <v-textarea
          v-show="post.type===`Article`"
          v-model="post.description"
          label="简述"
          name="description"
          rows="10"
        />

        <v-textarea
          v-model="post.meta"
          label="元数据"
          name="meta"
        />
      </v-flex>

      <v-flex
        id="PostEditor"
        align-start
        column
        fill-height
        justify-space-between
      >
        <froala
          :config="editorConfig.config"
          :tag="editorConfig.tag"
          v-model="post.content" />
      </v-flex>
    </v-layout>

    <v-layout
      align-center
      column
      fill-height
      justify-start
      wrap>
      <v-flex
        lg4
        md12
        sm12
      >
        <div class="text-xs-center">
          <v-btn
            color="teal"
            dark
            round
            small
            @click="SaveOrUpdate"> 提交
          </v-btn>
        </div>
      </v-flex>
    </v-layout>

  </v-container>
</template>

<script>
const Qs = require('qs')

export default {
  name: 'Editor',
  data: () => ({
    new: true,
    type_items: [],
    st_items: [],
    tag_items: [],
    full_style: false,

    msgCtx: {
      msg: null,
      color: 'success',
      snackbar: false
    },

    editorConfig: {
      tag: 'textarea',
      config: {
        placeholderText: '写点什么... ',
        height: 450,
        autofocus: true,
        charCounterCount: true,
        toolbarSticky: false,
        toolbarButtons: [
          'quote', 'bold', 'italic', 'underline', 'strikeThrough', 'subscript', 'superscript', '|',
          'fontFamily', 'fontSize', 'color', 'inlineClass', 'inlineStyle', 'paragraphStyle', 'lineHeight', '|',
          'paragraphFormat', 'align', 'formatOL', 'formatUL', 'outdent', 'indent', '-',
          'insertLink', 'insertImage', 'embedly', 'insertTable', 'emoticons', 'fontAwesome', 'insertHR', '|',
          'selectAll', 'undo', 'redo', 'clearFormatting', 'html', 'fullscreen']
      }
    },

    post: {
      title: null,
      alias: null,

      type: null,
      status: null,
      tags: null,

      description: null,
      meta: null,
      content: null
    }
  }),
  mounted: function () {
    let vm = this

    vm.load_tag()
    vm.load_type()
    vm.load_status()

    if (vm.$route.params.post_id) {
      let pid = vm.$route.params.post_id
      vm.$data.pid = pid
      vm.load_content(pid)
    }
  },

  methods: {

    SaveOrUpdate () {
      let vm = this
      let { reqInfo, data, ty } = { reqInfo: {}, data: vm.generate_data(), ty: 'post' }

      switch (vm.$data.post.type) {
        case 'Page':
          ty = 'page'
          delete data['tags']
          delete data['description']
          break
      }

      reqInfo.method = vm.$data.new ? vm.$http.post : vm.$http.put
      reqInfo.url = vm.$data.new ? '/api/posts' : '/api/post/' + vm.$data.pid

      reqInfo.method(vm.$gc.H + reqInfo.url, Qs.stringify(data))
        .then(function (response) {
          if (reqInfo.method === vm.$http.put) {
            let msg = { group: 'flush', type: 'success', text: '成功更新' }
            vm.$notify(msg)
            window.location.href = `/${ty}/${vm.$data.pid}`
          } else {
            window.location.href = response.data
          }
        })
        .catch((err) => {
          let msg = { group: 'flush', type: 'fail', text: err.response.status }
          vm.$notify(msg)
          // TODO： local save in browser
        })
    },

    generate_data () {
      let vm = this

      return {
        id: vm.$data.pid,
        title: vm.$data.post.title,
        alias: vm.$data.post.alias,
        type: vm.$data.post.type,
        status: vm.$data.post.status,
        add_date: new Date().toISOString().split('.')[0],
        tags: vm.$data.post.tags && vm.$data.post.tags.join(','),
        description: vm.$data.post.description,
        meta: vm.$data.post.meta,
        content: vm.$data.post.content
      }
    },

    load_content (pid) {
      let vm = this

      vm.$http.get(vm.$gc.H + '/api/post/' + pid)
        .then(function (response) {
          vm.$data.post = response.data
          vm.$data.new = false

          vm.$notify({ group: 'flush', type: 'success', text: '内容加载完成!' })
        })
    },

    load_tag () {
      let vm = this

      vm.$http.get(vm.$gc.H + '/tags')
        .then(function (response) {
          vm.$data.tag_items = response.data

          vm.$notify({ group: 'flush', type: 'success', text: '标签加载完成!' })
        })
    },

    load_status () {
      let vm = this
      vm.$http.get(vm.$gc.H + '/pub/query/status_type')
        .then(function (response) {
          vm.$data.st_items = response.data.items
          vm.$data.post.status = vm.$data.new ? response.data.default : vm.$data.post.status

          vm.$notify({ group: 'flush', type: 'success', text: '状态类型加载完成!' })
        })
    },

    load_type () {
      let vm = this

      vm.$http.get(vm.$gc.H + '/pub/query/post_type')
        .then(function (response) {
          vm.$data.type_items = response.data.items
          vm.$data.post.type = vm.$data.new ? response.data.default : vm.$data.post.type

          vm.$notify({ group: 'flush', type: 'success', text: '文章类型加载完成!' })
        })
    }
  }
}
</script>

<style>
#PostEditor div.fr-wrapper div:first-child {
  position: absolute;top: -10000px;opacity: 0;
}
</style>
